.search-bar {
  padding: 10px;
  border: 1px solid #ccc;
  width: 100%;
  border-radius: 5px;
  background-color: #f2f2f2;
  font-size: 14px;
}

.form-field {
  position: relative;
  display: inline-block;
}

.icon {
  position: absolute;
  right: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  background: white;
  padding: 0;
}

::selection {
  color: #fff;
  background: #664aff;
}

.shell {
  height: calc(100% - 20px);
  position: absolute;
}

.footer {
  height: 20px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #fff;
  color: #cc0000;
  font-size: 12px;
  bottom: 0;
  left: 0;
  width: 100%;
}
.wrap {
  height: 100%;
  display: flex;
}
.box {
  padding: 20px;
  /* height: 100vh; */
  width: 50vw;
  overflow: scroll;
}

.container {
  max-width: 450px;
  margin: 150px auto;
}

.container .searchInput {
  background: #fff;
  width: 100%;
  border-radius: 5px;
  position: relative;
  box-shadow: 0px 1px 5px 3px rgba(0, 0, 0, 0.12);
}
.searchInput {
  background: #ececec;
  position: relative;
  display: inline-block;
  width: 100%;
}

.searchInput input {
  height: 55px;
  width: 100%;
  background: #ececec;
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 0 60px 0 20px;
  font-size: 18px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
}

.searchInput.active input {
  border-radius: 5px 5px 0 0;
}

.searchInput .resultBox {
  background: #ececec;
  padding: 0;
  opacity: 0;
  pointer-events: none;
  max-height: 280px;
  overflow-y: auto;
}

.searchInput.active .resultBox {
  padding: 10px 8px;
  opacity: 1;
  pointer-events: auto;
}

.resultBox li {
  list-style: none;
  padding: 8px 12px;
  display: none;
  width: 100%;
  cursor: default;
  border-radius: 3px;
}

.searchInput.active .resultBox li {
  display: block;
}
.resultBox li:hover {
  background: #ff575170;
}

.searchInput .icon {
  position: absolute;
  right: 0px;
  top: 0px;
  height: 55px;
  width: 55px;
  text-align: center;
  line-height: 55px;
  font-size: 20px;
  cursor: pointer;
}

.chip {
  cursor: pointer;
  margin-bottom: 10px;
  margin-right: 10px;
  display: inline-block;
  padding: 0 12px;
  height: 30px;
  font-size: 12px;
  line-height: 30px;
  border-radius: 12px;
  background-color: #f1f1f1;
}

.chip img {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.closebtn {
  padding-left: 10px;
  color: #888;
  font-weight: bold;
  float: right;
  font-size: 20px;
  cursor: pointer;
}

.closebtn:hover {
  color: #000;
}

.pagination-bar {
  width: 12%;
  margin: 0px 44%;
  padding: 4px;
  display: flex;
  justify-content: center;
  background: #ff908c;
  border-radius: 25px;
}

